import React, { useState } from 'react'

const LoginFooter = () => {
  const [modalOpen, setModalOpen] = useState(false)
  const [modalTitle, setModalTitle] = useState('')
  const [modalContent, setModalContent] = useState('')

  const openModal = (title: string, content: string) => {
    setModalTitle(title)
    setModalContent(content)
    setModalOpen(true)
  }

  const closeModal = () => {
    setModalOpen(false)
    setModalTitle('')
    setModalContent('')
  }

  const handleLinkClick = (e: React.MouseEvent, type: 'terms' | 'privacy' | 'contact') => {
    e.preventDefault()
    let title = ''
    let content = ''

    switch (type) {
      case 'terms':
        title = 'Termos de Uso'
        content = '<strong>1. Introduo</strong><br/>Estes Termos de Uso regem o uso do nosso servio. Ao acessar ou usar o servio, voc concorda em estar vinculado a estes Termos. Se voc no concorda com estes Termos, no use o servio.<br/><br/><strong>2. Uso do Servio</strong><br/>Voc concorda em usar o servio apenas para fins lcitos e de acordo com estes Termos. Voc no deve usar o servio de forma que possa danificar, desabilitar, sobrecarregar ou prejudicar o servio.<br/><br/><strong>3. Propriedade Intelectual</strong><br/>Todo o contedo do servio, incluindo texto, grficos, logotipos e imagens,  de nossa propriedade ou licenciado para ns e  protegido por leis de direitos autorais e outras leis de propriedade intelectual.<br/><br/><strong>4. Limitao de Responsabilidade</strong><br/>Em nenhuma circunstncia seremos responsveis por quaisquer danos diretos, indiretos, incidentais, especiais ou consequenciais resultantes do uso ou da incapacidade de usar o servio.<br/><br/>Esta  uma verso simplificada e genrica dos Termos de Uso. <strong> fundamental substitu-la por termos legais completos e especficos para o seu servio.</strong>'
        break
      case 'privacy':
        title = 'Poltica de Privacidade'
        content = '<strong>Introduo:</strong> Esta Poltica de Privacidade descreve como coletamos, usamos e protegemos suas informaes pessoais.<br/>Ao usar nosso servio, voc concorda com a coleta e uso de informaes de acordo com esta poltica.<br/><br/><strong>Coleta de Informaes:</strong> Coletamos informaes que voc nos fornece diretamente, como nome e email, e automaticamente, como dados de uso e cookies.<br/><br/><strong>Uso das Informaes:</strong> Utilizamos essas informaes para operar e melhorar o servio, personalizar sua experincia e comunicar com voc.<br/><br/><strong>Compartilhamento de Informaes:</strong> No compartilhamos suas informaes pessoais com terceiros, exceto quando necessrio para fornecer o servio ou por exigncia legal.<br/><br/><strong>Seus Direitos:</strong> Voc tem o direito de acessar, corrigir ou excluir suas informaes.<br/><br/><strong>Importante:</strong> Esta  uma poltica genrica e deve ser substituda por uma poltica real e completa.<br/><br/>ltima atualizao: 25 de Julho de 2024.'
        break
      case 'contact':
        title = 'Contato'
        content = 'Para entrar em contato conosco, utilize as seguintes informaes:<br/><br/><strong>Email:</strong> contato@seuservico.com<br/><strong>Telefone:</strong> (XX) XXXX-XXXX<br/><strong>Endereo:</strong> Rua Exemplo, 123, Cidade, Estado, CEP<br/><br/>Responderemos o mais breve possvel. Por favor, inclua detalhes relevantes em sua mensagem para que possamos ajudar de forma eficiente.<br/><br/>Este  um texto de contato simulado. <strong>Substitua estas informaes pelas suas informaes de contato reais.</strong>'
        break
      default:
        break
    }

    openModal(title, content)
  }

  return (
    <footer className="bg-white">
      <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center">
          <div className="text-sm text-gray-500">
             2024 SENAI. Todos os direitos reservados.
          </div>
          <div className="flex space-x-6">
            <a href="#" className="text-gray-400 hover:text-gray-500" onClick={(e) => handleLinkClick(e, 'terms')}>
              Termos de Uso
            </a>
            <a href="#" className="text-gray-400 hover:text-gray-500" onClick={(e) => handleLinkClick(e, 'privacy')}>
              Poltica de Privacidade
            </a>
            <a href="#" className="text-gray-400 hover:text-gray-500" onClick={(e) => handleLinkClick(e, 'contact')}>
              Contato
            </a>
          </div>
        </div>
      </div>

      {/* Modal */}
      {modalOpen && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
          <div className="bg-white rounded-lg overflow-hidden shadow-xl transform transition-all sm:max-w-lg sm:w-full">
            <div className="px-4 py-3 border-b border-gray-200">
              <h3 className="text-lg leading-6 font-medium text-gray-900">
                {modalTitle}
              </h3>
            </div>
            <div className="px-4 py-5 sm:p-6">
              <p className="text-sm text-gray-500"
                dangerouslySetInnerHTML={{ __html: modalContent }}
              >
              </p>
            </div>
            <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
              <button
                type="button"
                className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm"
                onClick={closeModal}
              >
                Fechar
              </button>
            </div>
          </div>
        </div>
      )}
    </footer>
  )
}

export default LoginFooter 