import React from 'react' // useState removido, pois o estado do carrossel no  mais necessrio
import EventCard from './EventCard'

// Importar imagens - Ajuste os caminhos se necessrio com base nos locais reais dos arquivos
import imgMundoSenai from '../../../../assets/images/Imagens/010-Saiba mais - Eventos e Noticias.jpg' // Placeholder - Substitua pela imagem real do evento Mundo Senai, se disponvel
import imgStudent from '../../../../assets/images/Imagens/011-Eventos e Noticias.png' // Placeholder - Substitua pela imagem real do estudante, se disponvel
import imgWorkshop from '../../../../assets/images/Imagens/012-Saiba mais - Eventos e Noticias.jpg' // Placeholder - Substitua pela imagem real do workshop, se disponvel
import imgBuilding from '../../../../assets/images/Imagens/013-Saiba mais - Eventos e Noticias.jpg' // Placeholder - Substitua pela imagem real do prdio, se disponvel

const EventsSection: React.FC = () => {
  // Definir dados dos eventos com base na estrutura da imagem de referncia
  // Removido o 5 item, pois era apenas para repetio do carrossel
  const eventsData = [
    {
      id: 1,
      imageUrl: imgMundoSenai,
      isLarge: true, // Sinalizador para o card grande
      mainTitle: 'VEM A MUNDO SENAI 2024',
      dateText: 'DIAS 7, 8 E 9 DE AGOSTO.',
      hashtag: '#TROLANDONOSENAI',
      buttonLink: '#' // Link para o boto Saber Mais
    },
    {
      id: 2,
      imageUrl: imgStudent,
      isLarge: false,
      buttonLink: '#' // Link para o boto Saber Mais
    },
    {
      id: 3,
      imageUrl: imgWorkshop,
      isLarge: false,
      buttonLink: '#' // Link para o boto Saber Mais
    },
    {
      id: 4,
      imageUrl: imgBuilding,
      isLarge: false,
      buttonLink: '#' // Link para o boto Saber Mais
    }
    // Removido item com id: 5, pois o carrossel foi removido
  ]

  // Estado e manipuladores do carrossel removidos (useState, handlePrev, handleNext)

  // Dados separados para a linha superior e linha inferior (exibio esttica)
  const topRowEvents = eventsData.slice(0, 2) // Dois primeiros itens para a linha superior
  const bottomRowEvents = eventsData.slice(2) // Itens 3 e 4 para a linha inferior

  // Lgica de exibio do carrossel removida (displayedCarouselEvents, currentPageNumber, totalCarouselPages)

  return (
    // Continer da seo com preenchimento e fundo
    <section className="py-12 bg-gray-100">
      {' '}
      {/* Fundo alterado para cinza claro como na referncia */}
      <div className="container mx-auto px-4">
        {/* Ttulo da Seo */}
        <h2 className="text-3xl font-light text-center mb-10 text-gray-700 tracking-wider">
          EVENTOS E NOTCIAS
        </h2>{' '}
        {/* Estilizao ajustada para corresponder  referncia */}
        {/* Continer de grade para o layout assimtrico superior */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
          {/* Card Grande - Ocupando 2 colunas em telas mdias e maiores */}
          <div className="md:col-span-2">
            {topRowEvents[0] && (
              <EventCard
                key={topRowEvents[0].id}
                imageUrl={topRowEvents[0].imageUrl}
                isLarge={topRowEvents[0].isLarge}
                //mainTitle={topRowEvents[0].mainTitle}
                dateText={topRowEvents[0].dateText}
                //hashtag={topRowEvents[0].hashtag}
                buttonLink={topRowEvents[0].buttonLink} // Passar o link
              />
            )}
          </div>
          {/* Card Pequeno - Canto Superior Direito */}
          <div className="md:col-span-1">
            {topRowEvents[1] && (
              <EventCard
                key={topRowEvents[1].id}
                imageUrl={topRowEvents[1].imageUrl}
                isLarge={topRowEvents[1].isLarge}
                buttonLink={topRowEvents[1].buttonLink} // Passar o link
              />
            )}
          </div>
        </div>
        {/* Seo de Grade Esttica para a Linha Inferior (Substitui o Carrossel) */}
        {/* Div de wrapper de posicionamento relativo removida */}
        {/* Flex container para a linha inferior */}
        <div className="flex flex-col md:flex-row gap-4">
          {/* Card 3 (Estreito) */}
          {bottomRowEvents[0] && (
            <div className="w-full md:w-1/3">
              {' '}
              {/* Largura fixa em telas mdias e maiores */}
              <EventCard
                key={bottomRowEvents[0].id}
                imageUrl={bottomRowEvents[0].imageUrl}
                isLarge={bottomRowEvents[0].isLarge}
                buttonLink={bottomRowEvents[0].buttonLink}
              />
            </div>
          )}
          {/* Card 4 (Largo - Ocupa espao restante) */}
          {bottomRowEvents[1] && (
            <div className="flex-1">
              {' '}
              {/* Ocupa o espao restante */}
              <EventCard
                key={bottomRowEvents[1].id}
                imageUrl={bottomRowEvents[1].imageUrl}
                isLarge={bottomRowEvents[1].isLarge}
                buttonLink={bottomRowEvents[1].buttonLink}
              />
            </div>
          )}
        </div>
        {/* Controles do Carrossel Removidos */}
        {/* A div contendo botes e indicador de pgina foi removida */}
      </div>
    </section>
  )
}

export default EventsSection
